body {
  font-family: sans-serif;
  margin: 0;
  background-color: #7532a8;
}

.row {
  margin: 0;
}

.redirect {
  position: fixed;
  width: 100%;
  height: 100%;
}

.redirect span {
  font-size: 36px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}

.redirect a {
  font-weight: bold;
  color: red;
}

.redirect a:hover {
  text-decoration: none;
}
