.item {
  width: 100%;
  height: 100%;
}

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
}

.item:hover .inner {
  transform: rotateY(180deg);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.back {
  color: white;
  transform: rotateY(180deg);
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.back span {
  font-weight: bold;
  font-size: 28px;
  text-align: center;
}
