.wishForm {
  margin: 20px auto 0;
  border: 3px solid white;
  border-radius: 9px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 50%;
  max-width: 500px;
  min-width: 280px;
  font-size: 20px;
}

.wishForm label {
  display: block;
  margin-bottom: 10px;
  color: white;
}

.wishForm label:last-of-type {
  margin-top: 10px;
}

.wishForm input,
.wishForm textarea {
  font-size: 20px;
  border: none;
  border-radius: 3px;
  display: block;
  flex-basis: 100%;
  padding: 5px;
}

.wishForm textarea {
  resize: none;
}

.wishForm button {
  cursor: pointer;
  margin-top: 10px;
  font-size: 20px;
  display: block;
  align-self: center;
  font-weight: bold;
  color: white;
  background-color: #7532a8;
  border: 2px solid white;
  border-radius: 5px;
  padding: 5px 10px;
  transition: background-color 0.3s;
}

.wishForm button:hover {
  background-color: #56257c;
}
